import React from 'react';
import Tilt from 'react-tilt';
import './Logo.css';
import flogo from './fleminlogo.png';

const Logo = ()=> {
	return (
		<div className ='ma4 mt0'>
		<Tilt className="Tilt br2 shadow-2 grow	" 
		options={{ max : 40 }} style={{ height: 100, width: 200 }} >
        <div className="Tilt-inner pa3"><img style ={{paddingbottom:  '3px'}} 
                alt = 'logo' src = {flogo}/> </div>
               </Tilt>
        		</div>
        
        		);
}

export default Logo;